import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useState } from "react";
import CreateButton from "../../../../Ui/Button/CreateButton";
import AddDepartmentSiteLaboratory from "./AddDepartmentSiteLaboratory";
import { useTranslation } from "react-i18next";
import DepartmentSiteLaboratoryCard from "./DepartmentSiteLaboratoryCard";
import Loading from "../../../../Loader/Loading";
import { useDepartmentDashboardSite } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
const DepartmentSiteLaboratory = ({ did, dsid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);

  const {
    departmentDashboardSite,
    departmentDashboardSiteLoading,
    departmentDashboardSiteRefetch,
  } = useDepartmentDashboardSite({
    data: {
      did: did,
    },
    skip: !did,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  //
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("laboratory")}</h6>
            <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {departmentDashboardSite?.laboratory?.map((sub) => (
            <DepartmentSiteLaboratoryCard
              sub={sub}
              key={sub?._id}
              dsid={dsid}
              onRefetch={departmentDashboardSiteRefetch}
            />
          ))}
          {departmentDashboardSiteLoading && <Loading />}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <AddDepartmentSiteLaboratory
          onClose={onToggle}
          onRefetch={departmentDashboardSiteRefetch}
          dsid={dsid}
        />
      )}
    </>
  );
};

export default DepartmentSiteLaboratory;
