import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useFileUpload } from "../../../../../hooks/authentication-api";
import FormFile from "../../../../Ui/Input/FormFile";
import FormTextarea from "../../../../Ui/Input/FormTextarea";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../../../ImageCrop/CustomCrop";
import { useDepartmentAddLaboratory } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";

const AddDepartmentSiteLaboratory = ({
  dsid,
  onClose,
  onRefetch,
  onEditAction,
  menuData,
  isEdit,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    sub_head_title: "",
    sub_head_body: "",
    sub_heading_image: "",
    attach: "",
  });
  // const [activeCrop, setActiveCrop] = useState("");
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [fileUpload] = useFileUpload();

  const [departmentAddLaboratory] = useDepartmentAddLaboratory();

  useEffect(() => {
    if (isEdit && menuData) {
      setState({
        sub_head_title: menuData?.sub_head_title ?? "",
        sub_head_body: menuData?.sub_head_body ?? "",
        sub_heading_image: menuData?.sub_heading_image ?? "",
        attach: menuData?.attach ?? "",
      });
    }
  }, [isEdit, menuData]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };
  const onFileUploadAttach = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            attach: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            sub_heading_image: res.data?.imageKey,
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, [
      "sub_heading_image",
      "sub_head_body",
      "attach",
    ]);
    if (existingRequiredField(validation) && dsid) {
      if (isEdit) {
        onEditAction(state);
      } else {
        setDisabled((pre) => !pre);
        departmentAddLaboratory({
          dsid: dsid,
          bodyParameter: state,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {t(
                      isEdit
                        ? "update_department_laboratory"
                        : "add_department_laboratory"
                    )}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("sub_title")}
                placeholder={t("sub_title_placeholder")}
                name={"sub_head_title"}
                value={state.sub_head_title}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["sub_head_title"]}
              />
              <FormTextarea
                label={t("sub_body")}
                placeholder={t("sub_body_placeholder")}
                name={"sub_head_body"}
                value={state.sub_head_body}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["sub_head_body"]}
              />
              <FormFile
                label={t("sub_head_image")}
                name={"academic"}
                value={
                  state.sub_heading_image
                    ? {
                        key: state.sub_heading_image,
                        name: "View",
                      }
                    : ""
                }
                onChange={onFileUpload}
                errorShow={errorField["sub_heading_image"]}
              />
              <FormFile
                label={t("add_attachment")}
                name={"add_attachment"}
                value={
                  state.attach
                    ? {
                        key: state.attach,
                        name: "View",
                      }
                    : ""
                }
                onChange={onFileUploadAttach}
                errorShow={errorField["attach"]}
              />
              <CreateButton
                label={isEdit ? "update" : "add"}
                onAction={onAdd}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddDepartmentSiteLaboratory;
