import { useTranslation } from "react-i18next";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import DepartmentSiteLaboratoryCardMenu from "./DepartmentSiteLaboratoryCardMenu";
import { useState } from "react";
import FormFileView from "../../../../Ui/Input/FormFileView";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";

const DepartmentSiteLaboratoryCard = ({ sub, onRefetch, dsid }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.sf_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div
          className={style.sf_card_contatiner}
          style={{
            alignItems: "flex-start",
          }}
        >
          {sub?.sub_heading_image ? (
            <img
              src={`${imageShowUrl}/${sub?.sub_heading_image}`}
              style={{
                borderRadius: "6px",
                width: "5rem",
                height: "5rem",
              }}
              alt="user avatar"
            />
          ) : null}

          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {t("title")}
              {": "}
              {sub?.sub_head_title ?? ""}
            </h6>
            {sub?.attach ? (
              <FormFileView
                label={`${t("attachment")} :`}
                value={{
                  key: sub?.attach,
                }}
                customFormInputContainer={{
                  marginBlock: "0",
                }}
              />
            ) : null}
            <p>{sub?.sub_head_body ?? ""}</p>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>
      {open && (
        <DepartmentSiteLaboratoryCardMenu
          onClose={onToggle}
          dsid={dsid}
          menuData={sub}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default DepartmentSiteLaboratoryCard;
